@import "reset";

// TEXT
$h1-font-size: 32px;
$h1-line-height: 40px;

$h2-font-size: 28px;
$h2-line-height: 60px;

$h3-font-size: 24px;
$h3-line-height: 32px;

$h4-font-size: 20px;
$h4-line-height: 40px;

$h5-font-size: 16px;
$h5-line-height: 36px;

$p-font-size: 14px;
$p-line-height: 22px;


// header
$full-width-paddingX: 20px;

$main-width: 100%;
$main-max-width: 100%;

$header-height: 80px;

$logo-width: 180px;

$nav-buttons-margin-left: 30px;

$hamburger-size: 50px;


// main nav
$main-nav-padding: 140px 0 30px;
$main-nav-h5-margin-bottom: 1em;
$main-nav-h3-margin-bottom: 0.6em;
$nav-box-width: 20%;
$nav-box-sibling-margin-left: calc(20% / 3);
$main-nav-main-sibling-margin-top: 60px;
$main-nav-left-button-size: 50px;
$main-nav-left-button-font-size: 18px;


// hero
$hero-padding-top: 80px;
$headline-wrapper-margin-bottom: 40px;
$quickstart-button-padding: 0 50px;
$vendor-strip-height: 88px;
$vendor-strip-font-size: 16px;

// video
$video-section-height: 200px;

@import "size";

////////////////////////////////////////////////////////////////////////////////////////////////////
////////////////////////////////////////////////////////////////////////////////////////////////////
////////////////////////////////////////////////////////////////////////////////////////////////////

body {
  background-color: white; }

section {
  position: relative;
  background-color: white; }

section, header, footer {
  main {
    position: relative;
    margin: auto; } }

p {
  font-size: 14px;
  font-weight: 400; }


.button {
  display: inline-block;
  border-radius: 6px;
  padding: 0 20px;
  line-height: 40px;
  color: white;
  background-color: $blue;
  text-decoration: none;
  font-size: 1rem; }

#cellophane {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: none; }


// HEADER
header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 8888;
  background-color: transparent;
  box-shadow: 0 0 0 transparent;
  overflow: hidden;
  transition: 0.3s;
  text-align: center; }


.logo {
  position: relative;
  float: left;
  display: block;
  width: 180px;
  height: 88px;
  top: 0;
  left: 0;
  transform: none;
  background-image: url(/images/nav_logo.png);
  background-size: contain;
  background-position: center center;
  background-repeat: no-repeat; }


// Blog post tables
.blog-content {
  table {
    max-width: 100%;
    border: 1px solid #ccc;
    border-spacing: 0;
    margin-top: 30px;
    margin-bottom: 30px;

    thead {
      border-bottom: 2px solid #ccc;

      tr {
        th {
          padding: .5rem;
          font-size: 1.5rem; } } }

    tbody {
      tr {
        td {
          padding: .5rem;
          border-bottom: 1px solid #ccc;
          border-left: 1px solid #ccc;
          padding-right: 5rem; } } } } }

#docs {
  .flyout-button {
    position: fixed;
    top: 20px;
    left: 20px;
    width: 50px;
    height: 50px;
    background-image: url(/images/toc_icon.png);
    background-position: center center;
    background-repeat: no-repeat;
    background-size: auto;
    border-radius: 50%;
    transition: 0.3s;
    z-index: 99999; }

  &.open-nav .flyout-button {
    display: none; }

  .logo {
    position: absolute;
    top: 40px;
    left: 50%;
    transform: translate(-50%, -50%);
    display: block;
    width: 45px;
    height: 44px;
    background-image: url(/images/favicon.png); }

  &.flip-nav .flyout-button {
    background-image: url(/images/toc_icon_grey.png); } }


.nav-buttons {
  float: right; }

#viewDocs, #tryKubernetes {
  display: none; }

#viewDocs {
  border: 2px solid white;
  background-color: transparent;
  transition: 0.3s;

  &:hover {
    background-color: white;
    color: $dark-grey; } }

#tryKubernetes {
  width: 0;
  padding: 0 0;
  border: 1px solid transparent;
  background-color: transparent;
  text-align: center;
  white-space: nowrap;
  vertical-align: middle;
  overflow: hidden;
  transition: 0.3s; }

#hamburger {
  display: inline-block;
  position: relative;
  vertical-align: middle;
  padding: 0;
  border: 0;
  background: none;

  div, &:before, &:after {
    position: absolute;
    left: 15%;
    width: 70%;
    height: 2px;
    background-color: $blue;
    transition: 0.3s;
    content: ""; }

  div {
    top: calc(50% - 1px); }

  &:before {
    top: 24%; }

  &:after {
    bottom: 24%; }

  &:hover {
    div, &:before, &:after {
      background-color: white; } } }

#mainNav {
  h5 {
    color: $blue;
    font-weight: normal; }

  main {
    white-space: nowrap;
    overflow: hidden;
    clear: both; }

  .nav-box {
    float: left;
    white-space: normal; }

  h3 {
    a {
      color: $blue;
      text-decoration: none; } } }

// Global Nav - 12/9/2016 Update

ul.global-nav {
  display: none;

  li {
    display: inline-block;
    margin-right: 14px;

    a {
      color: #fff;
      font-weight: 400;
      padding: 0;
      position: relative;

      &.active:after {
        position: absolute;
        width: 100%;
        height: 2px;
        content: '';
        bottom: -4px;
        left: 0;
        background: #fff; }

      .ui-icon {
        filter: brightness(0) invert(1); } }

    ul {
      display: none;
      position: fixed;
      top: 40px;
      text-align: left;

      li {
        display: block;
        height: 28px;

        a {
          background: $dark-grey;
          color: #fff;
          padding: 7px; } }

      li:last-child {
        a {
          border-radius: 7px; } } } }

  li:hover {
    ul {
      display: block; } } }


.flip-nav, .open-nav {
  ul.global-nav li a {
    color: $dark-grey;

    .ui-icon {
      filter: brightness(0); } } }

.flip-nav ul.global-nav li ul li a {
  background: #fff;
  color: $dark-grey; }

.flip-nav ul.global-nav li a.active:after, .flip-nav ul.global-nav li ul li a.active:after, .open-nav ul.global-nav li a.active:after {
  background: $blue; }

// FLIP NAV
.flip-nav {
  header {
    background-color: white; } }


// OPEN NAV
.open-nav {
  body {
    overflow: hidden; }

  #cellophane {
    display: block;
    z-index: 9998; }

  header {
    background-color: #e8e8e8;
    z-index: 9999; }

  #hamburger {
    div {
      opacity: 0; }

    &:before, &:after {
      left: 12px;
      transform-origin: 0 1px; }

    &:before {
      transform: rotate(45deg); }

    &:after {
      transform: rotate(-45deg); } } }

.open-nav, .y-enough {
  #tryKubernetes {
    width: auto;
    padding: 0 20px;
    background-color: $blue;
    border-color: $blue; } }


.flip-nav, .open-nav {
  header {
    box-shadow: 0 1px 2px $medium-grey; }

  #viewDocs {
    border-color: $dark-grey;
    color: $dark-grey;

    &:hover {
      border-color: $blue;
      background-color: $blue;
      color: white; } }

  #hamburger:hover {
    div, &:before, &:after {
      background-color: $dark-grey; } } }



// HERO
#hero {
  background-image: url(/images/texture.png);
  background-color: $dark-grey;
  text-align: center;
  padding-left: 0;
  padding-right: 0;
  margin-bottom: 0;
  position: relative;

  &.bot-bar:after {
    display: block;
    margin-bottom: -20px;
    height: 8px;
    width: 100%;
    background-color: transparentize(white, 0.9);
    content: ''; }

  &.no-sub {

    h5 {
      display: none; }

    h1 {
      margin-bottom: 20px; } } }

#home #hero:after {
