// VENDOR STRIP
#vendorStrip {
  position: relative;
  background-color: transparentize(white, 0.9);
  font-weight: 100;
  white-space: nowrap;
  text-align: center;

  li a {
    color: transparentize(white, 0.5);

    &.YAH {
      color: white;
      position: relative; } } }


// FOOTER
footer {
  width: 100%;
  background-image: url(/images/texture.png);
