  h1, h2, h3, h4, h5, h6 {
    line-height: normal;
    font-weight: 500;
    margin-bottom: 30px;
    padding-bottom: 10px;

    // Make sure anchor links aren't hidden by the header
    &:before {
      display: block;
      content: " ";
      margin-top: -$header-clearance;
      height: $header-clearance;
      visibility: hidden; } }

