// Video thingy
#video {
  height: $video-section-height; }

#video {
  width: 100%;
  position: relative;
  background-position: center center;
  background-size: cover;


  & > .light-text {
    display: none;
    position: absolute;
    top: 50%;
    left: 75%;
    width: 525px;
    padding-right: 80px;
    transform: translate(-50%, -50%);
    color: white; }

  h2 {
    font-size: 32px;
    line-height: 44px;
    margin-bottom: 20px; }

  p {
    margin-bottom: 20px; }

  #desktopKCButton {
    position: relative;
    font-size: 18px;
    background-color: $dark-grey;
    border-radius: 8px;
    color: $white;
    padding: 20px 10px 20px 10px; }

  #desktopShowVideoButton {
    position: relative;
    font-size: 24px;
    background-color: white;
    border-radius: 8px;
    color: $blue;
    padding: 15px 30px 15px 80px;
    margin-bottom: 15px;

    &:before {
      content: "";
      position: absolute;
      @include pureCenter(40px);
      width: 0;
      height: 0;
      border-style: solid;
      border-width: 10px 0 10px 20px;
      border-color: transparent transparent transparent $blue; } }

  #mobileShowVideoButton {
    @include pureCenter;
    width: 80px;
    height: 80px;
    border-radius: 50%;
    background-color: transparent;
    border: 5px solid rgba(255, 255, 255, 0.2);
    overflow: visible;

    &:after {
      @include pureCenter;
      left: 40px;
      content: "";
      width: 0;
      height: 0;
      border-style: solid;
      border-width: 20px 0 20px 30px;
      border-color: transparent transparent transparent #ffffff; } } }

#videoPlayer {
  @include fullScreen;
  background-color: rgba(0, 0, 0, 0.9);
  display: none;

  iframe {
    @include pureCenter;
    @include maintain-aspect-ratio; }

  #closeButton {
    position: absolute;
    top: 20px;
    right: 20px;
    width: 50px;
    height: 50px;
    border: 2px solid transparent;
    transition: 0.3s;

    &:before, &:after {
      content: "";
      position: absolute;
      top: calc(50% - 1px);
      left: 10%;
      width: 80%;
      height: 2px;
      background-color: white; }

    &:before {
      transform: rotate(45deg); }

    &:after {
      transform: rotate(-45deg); }

    &:hover {
      border-color: white; } } }

